<template>
  <div class="home">
    <div v-if="isAuthenticated">
    <h3>Создание игры</h3>
    <router-link  to="/game"><button class="buttons">Создать</button></router-link>
    </div>
    <div v-else>
    <h3>Подключиться к игре</h3>
    <input type="text" class="textboxes"><router-link to="/game"><input type="button" value="Подключиться" class="buttons"></router-link>   
  </div>
  </div>
</template>

<script>
export default {
computed:
  {
    isAuthenticated() {
      return this.user != null;
    },
    user() { return this.$store.state.auth.user},
  }
}
</script>
